import React, {Component} from "react";
import {connect} from "react-redux";
import {getBusinessDataSource} from "../selectors";

class MulticartBpgInfoComponent extends Component {
    render() {
        return this.props.businessDataSource === 'bpg' && (
            <div className="opl-msg opl-msg--box opl-msg--info u-padding-all u-margin-top-l">
                <p className="u-padding-left u-font-bold g-black1-c u-inline-block">{this.props.descriptions.text || this.props.defaultText}</p>
            </div>
        );
    }
}


const mapStateToProps = state => ({
    businessDataSource: getBusinessDataSource(state)
})

const mapDispatchToProps = dispatch => ({
})

MulticartBpgInfoComponent.defaultProps = {
    defaultText: "Twoje dane zostały pobrane z bazy. Sprawdź ich poprawność."
}

export default connect(mapStateToProps, mapDispatchToProps)(MulticartBpgInfoComponent);